@import '../variables';


.hero {
    display: flex;
    flex-direction: column;
    justify-content: center;
    

    width: 100vw;
    margin-top: 10vh;

    .quote {

        color: $darkest;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;

        .quote-text {
            font-style: italic;
            font-size: 1.5rem;
            text-align: center;
        }
        .quote-author {
            font-size: 1.8rem;
            font-weight: bold;
        }

        margin-bottom: 5rem;
    }


    .coin-feed {
        padding: 2rem 0 6rem 0 ;
        display: grid;
        grid-template-rows: repeat(7, 3rem);
        justify-content: center;
        row-gap: 3rem;
        background-color: $light;
        

        .coin-container{
            display: grid;
            grid-template-columns: repeat(5, 5rem);
            column-gap: 3rem;
            align-items: center;
            justify-content: center;
            text-align: center;
            .up{
                color: darken($up, 20%);
            }
            .down {
                color: darken($down, 20%);
            }
        }


    }

    @media only screen and (max-width:900px) {
        .coin-feed {
            padding: 2rem 0 6rem 0 ;
            display: grid;
            grid-template-rows: repeat(7, 3rem);
            justify-content: center;
            row-gap: 3rem;
            background-color: $light;
            
    
            .coin-container{
                display: grid;
                grid-template-columns: repeat(5, 3rem);
                column-gap: 1.5rem;
                align-items: center;
                justify-content: center;
                text-align: center;
                .up{
                    color: darken($up, 20%);
                }
                .down {
                    color: darken($down, 20%);
                }
            }
    
    
        }
    
    }
}


